/* stylelint-disable-next-line scss/at-rule-no-unknown */
@import-normalize;
@custom-media --tablet (width >= 768px);
@custom-media --desktop (width >= 1024px);
@custom-media --wide (width >= 1280px);
@custom-media --only-mobile (width < 768px);
@custom-media --only-tablet (width >= 768px) and (width < 1024px);
@custom-media --only-desktop (width >= 1024px) and (width < 1280px);
/*
    examples:
    $color-white: #FFFFFF;
    $color-white-two: #DBDBDB;
*/
/*
    general variables

    examples:

    $transition-default: 0.3s linear;
*/
/*
    create variables for each font-family with fallback
    create mixin for each font-family with default font-weight
*/
/* general mixins and helpers */
/*
   typography
 */
/*
    put @font-face rules ordered by font-weight within the same font-family
    use woff, woff2
    do not use quotes if you don't have space in font name
 */
/*  reset default styles */
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0; }

ul {
  list-style: none;
  margin: 0;
  padding: 0; }

img {
  vertical-align: top; }

/* general grid styles */
/*
    general layout styles
    put .container here or other global layout classes

    example:

    .container {
        max-width: $containerMaxWidth;
        margin-left: auto;
        margin-right: auto;
    }

*/
*,
*::before,
*::after {
  box-sizing: border-box; }

.html {
  position: relative;
  overflow-x: hidden;
  font-size: 100%; }
  .html.is-blocked {
    overflow: hidden; }
  .html.is-blocked-touch {
    position: fixed;
    overflow-y: scroll;
    width: 100%;
    height: auto; }

.body {
  -webkit-font-smoothing: antialiased;
  overflow-x: hidden;
  font-size: 100%; }

.visually-hidden {
  position: absolute;
  overflow: hidden;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  white-space: nowrap;
  -webkit-clip-path: inset(100%); }

/*
    general buttons styles
    use .btn as namespace
*/
/* global styles for SVG */
.icon {
  fill: currentColor;
  vertical-align: top; }
  .icon path {
    fill: inherit; }
